import authenticatedApi from "./authenticatedApi";

class UserService {
  async getCountByRole() {
    const response = await authenticatedApi.get(`/user/count_by_role`);
    return response.data;
  }

  async getAllUsers() {
    const response = await authenticatedApi.get(`/user/all`);
    return response.data;
  }

  async getUser({ userId }) {
    const response = await authenticatedApi.get(`/user/${userId}`);
    return response.data;
  }

  async deleteUser({ userId }) {
    const response = await authenticatedApi.delete(`/user/${userId}`);
    return response.data;
  }

  async createUser(data) {
    const response = await authenticatedApi.post(`/user/create`, data);
    return response.data;
  }

  async updateUser({ userId, data }) {
    const response = await authenticatedApi.put(`/user/${userId}`, data);
    return response.data;
  }

  async updateUserProfile(data) {
    const response = await authenticatedApi.put(`/user/profile`, data);
    return response.data;
  }
}

export default new UserService();
